<template>
    <v-container fluid ma-0 pa-0 full-height>
        <v-card>
            <v-container fluid ma-0 pa-0 full-height>
                <h2 class="text-md-center mt-5">Penyusutan</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Data Penyusutan
                        </v-btn>

                        <v-btn depressed dark rounded style="text-transform: none !important;" color="blue accent-3"
                            class="ml-3" :to="'/listinventaris'" link>
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah List Inventaris
                        </v-btn>

                        <v-btn depressed dark rounded style="text-transform: none !important;" color="indigo accent-3"
                            class="ml-3" @click="postingBtn()">
                            <v-icon size="18" class="mr-2">mdi-arrow-up-bold-box-outline</v-icon>
                            Posting
                        </v-btn>
                        
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" dense single-line outlined hide-details>
                        </v-text-field>
                    </v-flex>

                    <v-col cols="2">
                        <v-menu ref="menu3" v-model="menu3" :close-on-content-click="false"
                            :return-value.sync="date" transition="scale-transition" offset-y
                            min-width="290px">
                            <template v-slot:activator="{ on }">
                                <v-text-field v-model="form3.startdate" label="Start Date"
                                    dense single-line outlined v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="form3.startdate" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu3 = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.menu3.save(date)">OK</v-btn>
                            </v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="2">
                        <v-menu ref="menu4" v-model="menu4" :close-on-content-click="false"
                            :return-value.sync="date" transition="scale-transition" offset-y
                            min-width="290px">
                            <template v-slot:activator="{ on }">
                                <v-text-field v-model="form3.enddate" label="End Date"
                                    dense single-line outlined v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="form3.enddate" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu4 = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.menu4.save(date), trigger=true">OK</v-btn>
                            </v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="1">
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="mr-4" @click="getData(), trigger=false">
                            <v-icon size="18">mdi-magnify</v-icon>
                        </v-btn>
                    </v-col>

                </v-layout>
                <v-data-table :headers="headers" :items="penyusutans" :search="keyword">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.id">
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["tglperolehan"] }} </td>
                                <td class="title">{{ item["namabarang"] }} </td>
                                <!-- <td class="title">{{ item["jenisinventaris"]}}</td>
                                <td class="title">{{ item["kelbarang"] }}</td> -->
                                <td class="title">{{ item["noperkiraan"].slice(0,3) + '-'  + item["noperkiraan"].slice(3,5) + '-' + item["noperkiraan"].slice(5,9)}}</td>
                                <td class="title">{{ item["umur"] }} Bln</td>
                                <td class="title">Rp {{ item["nilaiperolehan"] | currency}}</td>
                                <td class="title">Rp {{ item["nilaiawalbuku"] | currency}}</td>
                                <td class="title">Rp {{ item["nilaipenyusutan"] | currency}}</td>
                                <td class="title">
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click="deleteData(item)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
    
        <v-dialog v-model="dialog" persistent max-width="1000" @keydown.esc="dialog = false">
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Data Penyusutan</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="2">
                                <h3>Tanggal</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition" offset-y
                                    min-width="290px">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field v-model="form2.tglperolehan" label="Tanggal Perolehan"
                                            dense single-line outlined readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="form2.tglperolehan" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="1"></v-col>

                            <v-col cols="2" class="mt-2 ml-3">
                                <h3>Jenis Inventaris</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-autocomplete dense single-line outlined :items="listjenis" item-text="nama"
                                    item-value="kodeper" label="Jenis Inventaris*" v-model="form2.jenisinventaris">
                                </v-autocomplete>
                            </v-col>

                            <v-col cols="12" class="overflow-y-auto" style="max-height:420px;">
                                <v-row class="mr-2 scroll-y" v-for="(forms, index) in form" :key="index" no-gutters>

                            <v-col cols="2" class="mt-2">
                                <h3>Nama Barang</h3>
                            </v-col>
                            <v-col class="mr-3" cols="3">
                                <v-text-field label="Nama Barang*" v-model="forms.namabarang" dense single-line outlined required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>

                            <v-col cols="2" class="mt-2">
                                <h3>Kelompok Barang</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-autocomplete dense single-line outlined :items="listpenyusutan" item-text="KelompokBarang"
                                    item-value="KodeKelompok" label="Kelompok Barang*" v-model="forms.kelbarang">
                                </v-autocomplete>
                            </v-col>
                            <!-- <v-col cols="2" class="mt-2">
                                <h3>No Perkiraan</h3>
                            </v-col>
                            <v-col class="mr-3" cols="3">
                                <v-text-field label="Nomor Perkiraan*" v-model="forms.noperkiraan" dense single-line outlined required>
                                </v-text-field>
                            </v-col> -->
                            <v-col cols="2" class="mt-2">
                                <h3>Umur (Bulan)</h3>
                            </v-col>
                            <v-col class="mr-3" cols="3">
                                <v-text-field label="Umur*" v-model="forms.umur" @keyup="pembagian()" dense single-line outlined required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2" class="mt-2">
                                <h3>Nilai Perolehan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field label="Nilai Perolehan*" v-model="forms.nilaiperolehan" @keyup="pembagian(), nilaiawal()" dense single-line outlined required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2" class="mt-2">
                                <h3>Nilai Awal Buku</h3>
                            </v-col>
                            <v-col class="mr-3" cols="3">
                                <v-text-field label="Nilai Awal Buku*" v-model="forms.nilaiawalbuku" @keyup="nilaiawal()" dense single-line outlined required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2" class="mt-2">
                                <h3>Nilai Penyusutan</h3>
                            </v-col>
                            <v-col class="mr-3" cols="3">
                                <v-text-field label="Nilai Penyusutan*" v-model="forms.nilaipenyusutan" dense single-line outlined required>
                                </v-text-field>
                            </v-col>
                            <h2>-----------------------------------------------------------------------------------------------------------
                            </h2>


                                </v-row>
                                <v-btn depressed dark rounded style="text-transform: none !important;"
                                    color="green accent-3" @click="addData">
                                    <v-icon size="18" class="mr-2">mdi-plus-thick</v-icon>
                                    Tambah Item
                                </v-btn>
                                <v-btn icon color="red" light @click="deleteIndex(index)">
                                    <v-icon>mdi-trash-can</v-icon>
                                </v-btn>

                            </v-col>

                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialog2" persistent max-width="950" @keydown.esc="dialog2 = false">
            <v-card>
                <v-card-title>
                    <span class="headline">Edit Data Penyusutan</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Tanggal</h3>
                            </v-col>
                                <v-col cols="2">
                                    <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false"
                                        :return-value.sync="date" transition="scale-transition" offset-y
                                        min-width="290px">
                                        <template v-slot:activator="{ on }">
                                            <v-text-field dense single-line outlined v-model="edit.tglperolehan"
                                                label="Tanggal Perolehan" readonly v-on="on"></v-text-field>
                                        </template>
                                        <v-date-picker v-model="edit.tglperolehan" no-title scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="menu2 = false">Cancel</v-btn>
                                            <v-btn text color="primary" @click="$refs.menu2.save(date)">OK</v-btn>
                                        </v-date-picker>
                                    </v-menu>
                                </v-col>
                            <v-col cols="2"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">No Perkiraan</h3>
                            </v-col>                            
                            <v-col  cols="3 ">
                                <v-text-field dense single-line outlined readonly label="Nomor Perkiraan*" v-model="edit.noperkiraan" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Nama Barang</h3>
                            </v-col>    
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nama Barang*" v-model="edit.namabarang" required></v-text-field>
                            </v-col>

                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Umur</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Umur*" v-model="edit.umur" @keyup="pembagian()" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Nilai Perolehan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nilai Perolehan*" v-model="edit.nilaiperolehan" @keyup="pembagian()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Nilai Awal</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nilai Awal*" v-model="edit.nilaiawalbuku"
                                    required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Nilai Penyusutan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nilai Penyusutan*" v-model="edit.nilaipenyusutan" @keyup="pembagian()" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="updateData()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialog3" max-width="600px" @keydown.esc="dialog3 = false">
            <v-card>
                <v-card-title>
                    <span class="headline" style="color:red">Warning!!!</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <h2>Anda Akan Melakukan Operasi Berikut:</h2>
                        <br>
                        <h3> 1. Melakukan Update Nilai Buku</h3>
                        <br>
                        <h3> 2. Melakukan Update Saldo Biaya Penyusutan (Tabel List Perkiraan)</h3>
                        <br>
                        <h3> 3. Melakukan Update Nilai Penyusutan = 0 Jika Nilai Buku 0</h3>
                        <br>
                        <h3> 4. Memasukkan Data ke Buku Jurnal</h3>
                        <br>
                        <h3> 5. Melakukan Update Saldo Biaya Inventaris Kantor (Tabel List Perkiraan)</h3>
                        <br>
                        <h3> 6. Melakukan Update Saldo Biaya Inventaris Pabrik (Tabel List Perkiraan)</h3>
                        <br>
                        <h3> 7. Melakukan Update Saldo Biaya Kendaraan (Tabel List Perkiraan)</h3>
                    </v-container>
                    <big>Apakah Anda yakin?</big>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="dialog3=false, trigger_insert=false">Cancel</v-btn>
                    <v-btn color="blue darken-1" text @click="getPosting()">Yes</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
export default {
    data() {
        return {
            date: new Date().toISOString().substr(0, 10),
            menu: false,
            menu2: false,
            menu3: false,
            menu4: false,
            trigger: false,
            statusUpdate: true,
            dialog: false,
            dialog2: false,
            dialog3: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                    class: 'title'
                },
                {
                    text: 'Tanggal Perolehan',
                    value: 'tglperolehan',
                    class: 'title'
                },
                {
                    text: 'Nama Barang',
                    value: 'namabarang',
                    class: 'title'
                },
                            
                // {
                //     text: 'Jenis Inventaris',
                //     value: 'jenisinventaris',
                //     class: 'title'
                // },
                // {
                //     text: 'Kelompok Barang',
                //     value: 'kelbarang',
                //     class: 'title'
                // },
                {
                    text: 'Nomor Perkiraan',
                    value: 'noperkiraan',
                    class: 'title'
                },
                {
                    text: 'Umur',
                    value: 'umur',
                    class: 'title'
                },
                {
                    text: 'Nilai Perolehan',
                    value: 'nilaiperolehan',
                    class: 'title'
                },
                {
                    text: 'Nilai Buku',
                    value: 'nilaiawalbuku',
                    class: 'title'
                },
                {
                    text: 'Nilai Penyusutan',
                    value: 'nilaipenyusutan',
                    class: 'title'
                },
                {
                    text: 'Aksi',
                    value: null,
                    class: 'title'
                },
            ],
            listpenyusutan: [],
            listjenis: [
                {
                    nama: 'Kantor',
                    kodeper: '1700'
                },
                {
                    nama: 'Pabrik',
                    kodeper: '1701'
                },
                {
                    nama: 'Mobil',
                    kodeper: '1650'
                },
                {
                    nama: 'Motor',
                    kodeper: '1651'
                },
                {
                    nama: 'Truk',
                    kodeper: '1652',
                }
                ],
            penyusutans: [],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            form3: {
                startdate: '',
                enddate: '',
            },
            form2: {
                tglperolehan: '',
                jenisinventaris: '',
            },
            form: [{
                tglperolehan: '',
                namabarang: '',
                jenisinventaris: '',
                kelbarang: '',
                noperkiraan: '',
                umur: '',
                nilaiperolehan: 0,
                nilaiawalbuku: 0,
                nilaipenyusutan: 0,
            }],
            edit: {
                tglperolehan: '',
                namabarang: '',
                jenisinventaris: '',
                kelbarang: '',
                noperkiraan: '',
                umur: '',
                nilaiperolehan: 0,
                nilaiawalbuku: 0,
                nilaipenyusutan: 0,
            },
            penyusutan: new FormData,
            typeInput: 'new',
            errors: '',
            total: 0,
            updatedId: '',
        }
    },
    methods: {
        getData() {
            var uri = this.$apiUrl + 'penyusutan'
            this.$http.get(uri).then(response => {
                this.penyusutans = response.data
                // for(var x in this.penyusutans){
                //     this.total += this.penyusutans[x].nilaipenyusutan
                //     console.log(this.total)
                // }
            })

        if(this.trigger == true){
            var uri = this.$apiUrl + 'penyusutan/' + this.form3.startdate + '/' + this.form3.enddate 
            this.$http.get(uri).then(response => {
                this.penyusutans = response.data
                this.resetForm3();
            })
        }

        },
        
        // Fill: function (item) {
        //     return item.tglperolehan == this.form3.startdate || item.tglperolehan == this.form3.enddate;
        // },  
        postingBtn() {
            this.dialog3 = true
        },

        getPosting(){
            var uri = this.$apiUrl + 'penyusutan/posting'
            this.$http.get(uri).then(response => {
                this.penyusutans = response.data
                console.log(this.penyusutans)
            })
            var uri2 = this.$apiUrl + 'penyusutan/postinglist'
            this.$http.get(uri2).response

            var uri3 = this.$apiUrl + 'penyusutan/setnilaipenyusutan'
            this.$http.get(uri3).response

            var uri4 = this.$apiUrl + 'penyusutan/setbukujurnal'
            this.$http.get(uri4).response

            var uri5 = this.$apiUrl + 'penyusutan/postingkantor'
            this.$http.get(uri5).response

            var uri6 = this.$apiUrl + 'penyusutan/postingpabrik'
            this.$http.get(uri6).response
            
            var uri7 = this.$apiUrl + 'penyusutan/postingkendaraan'
            this.$http.get(uri7).response

        },

        getListPenyusutan() {
            var uri = this.$apiUrl + 'listpenyusutan'
            this.$http.get(uri).then(response => {
                this.listpenyusutan = response.data 
            })
        },

        addData(){
            this.form.push({
                namabarang: '',
                kelbarang: '',
                noperkiraan: '',
                umur: 0,
                nilaiperolehan: 0,
                nilaiawalbuku: 0,
                nilaipenyusutan: 0,
                total: 0,
                menu: false 
            })
        },
        
        deleteIndex(index) {
            this.form.splice(index, 1);
        },

        Activity(key) {
            // untuk memposting aktivitas user yang login
            var activity = new FormData
            activity.append('activity', key)
            var uri = this.$apiUrl + 'activity'
            this.$http.post(uri, activity).then(response => {}).catch(error => {
                this.errors = error
            })
        },
        
        pembagian() {
            var i;
            for (i = 0; i < this.form.length; i++) {
                this.form[i].nilaipenyusutan = this.form[i].nilaiperolehan/(this.form[i].umur);
            }
            this.edit.nilaipenyusutan = this.edit.nilaiperolehan/this.edit.umur;

        },

        nilaiawal(){
            var j;
            for (j = 0; j < this.form.length; j++){
                this.form[j].nilaiawalbuku = this.form[j].nilaiperolehan;
            }
            
        },

        sendData() {
        var i;
        for (i = 0; i < this.form.length; i++) {
            this.penyusutan = new FormData
            this.penyusutan.append('tglperolehan', this.form2.tglperolehan);
            this.penyusutan.append('jenisinventaris', this.form2.jenisinventaris);
            this.penyusutan.append('namabarang', this.form[i].namabarang);
            this.penyusutan.append('kelbarang', this.form[i].kelbarang);
            this.form[i].noperkiraan = this.form2.jenisinventaris + this.form[i].kelbarang;

            this.penyusutan.append('noperkiraan', this.form[i].noperkiraan);
            this.penyusutan.append('umur', this.form[i].umur);
            this.penyusutan.append('nilaiperolehan', this.form[i].nilaiperolehan);
            this.penyusutan.append('nilaiawalbuku', this.form[i].nilaiawalbuku);
            this.penyusutan.append('nilaipenyusutan', this.form[i].nilaipenyusutan);

            var uri = this.$apiUrl + 'penyusutan'
            this.load = true
            this.$http.post(uri, this.penyusutan).then(response => {
                this.snackbar = true; //mengaktifkan snackbar
                this.color = 'green'; //memberi warna snackbar
                this.text = response.data; //memasukkan pesan ke snackbar
                this.load = false;
                this.dialog = false
                this.getData(); //mengambil data user
                this.resetForm();
                this.Activity('Melakukan Create Penyusutan');
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
                this.load = false;
            })

        }
      },
        updateData() {
            this.penyusutan.append('tglperolehan', this.edit.tglperolehan);
            this.penyusutan.append('namabarang', this.edit.namabarang);
            this.penyusutan.append('kelbarang', this.edit.kelbarang);
            this.penyusutan.append('jenisinventaris', this.edit.jenisinventaris);
            this.penyusutan.append('noperkiraan', this.edit.noperkiraan);
            this.penyusutan.append('umur', this.edit.umur);
            this.penyusutan.append('nilaiperolehan', this.edit.nilaiperolehan);
            this.penyusutan.append('nilaiawalbuku', this.edit.nilaiawalbuku);
            this.penyusutan.append('nilaipenyusutan', this.edit.nilaipenyusutan);
            var uri = this.$apiUrl + 'penyusutan/' + this.updatedId;
            this.load = true
            this.$http.post(uri, this.penyusutan).then(response => {
                this.snackbar = true; //mengaktifkan snackbar
                this.color = 'green'; //memberi warna snackbar
                this.text = response.data; //memasukkan pesan ke snackba
                this.load = false;
                this.dialog2 = false
                this.statusUpdate = true
                this.total = 0;
                this.getData(); //mengambil data user
                this.resetForm2();
                // this.Activity('Melakukan Update Penyusutan');
                this.typeInput = 'new';
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
                this.load = false;
                this.typeInput = 'new';
            })
        },
        editHandler(item) {
            this.typeInput = 'edit';            
            this.dialog2 = true;
            this.statusUpdate = false;
            this.edit.tglperolehan = item["tglperolehan"];
            this.edit.namabarang = item["namabarang"];
            this.edit.jenisinventaris = item["jenisinventaris"];
            this.edit.kelbarang = item["kelbarang"];
            this.edit.noperkiraan = item["noperkiraan"];
            this.edit.umur = item["umur"];
            this.edit.nilaiperolehan = item["nilaiperolehan"];
            this.edit.nilaiawalbuku = item["nilaiawalbuku"];
            this.edit.nilaipenyusutan = item["nilaipenyusutan"];
            this.updatedId = item["id"]
        },
        deleteData(item) { //mengahapus data
            var uri = this.$apiUrl +
                'penyusutan/' + item.id; //data dihapus berdasarkan id
            this.$http.delete(uri).then(response => {
                this.snackbar = true;
                this.text = response.data;
                this.color = 'green'
                this.deleteDialog = false;
                this.total = 0;
                this.getData();
                this.getSaldo()
                this.Activity('Melakukan Delete Penyusutan');
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
            })
        },
        setForm() {
            if (this.typeInput === 'new') {
                this.sendData()
            } else {
                this.updateData()
            }
        },
        close() {
            this.dialog = false
            this.dialog2 = false
            this.resetForm()
            this.resetForm2()
        },
        resetForm() {
            this.form = [{
                tglperolehan: '',
                namabarang: '',
                jenisinventaris: '',
                kelbarang: '',
                noperkiraan: '',
                umur: 0,
                nilaiperolehan: 0,
                nilaiawalbuku: 0,
                nilaipenyusutan: 0,
            }],
            this.form2 = {
                tglperolehan: '',
                jenisinventaris: '',
            },
            this.total = 0,

            this.statusUpdate = true
        },
        resetForm2() {
            this.edit = {
                tglperolehan: '',
                namabarang: '',
                jenisinventaris: '',
                kelbarang: '',
                noperkiraan: '',
                umur: 0,
                nilaiperolehan: 0,
                nilaiawalbuku: 0,
                nilaipenyusutan: 0,
            }
            this.statusUpdate = true
        },
        resetForm3(){
            this.form3 = {
                startdate: '',
                enddate: '',
            }
            this.trigger=false
        }
    },
    mounted() {
        this.getData();
        this.getListPenyusutan();
    },
    
}
</script>   